.nav__container {
	width: 100%;
	display: block;	
	padding-left: 140px;
	padding-top: 10px;
}
.nav__content {
	width: 100%;
	display: block;
}
.main-nav {
	display: flex;
	flex-wrap: wrap;
	float: left;

	> li {
		display: inline-block;
		float: left;
		margin-bottom: 11px;
		margin: 0 60px 10px; 

		> a {
			text-transform: uppercase;
			font: 14px/16px $font-family-halvar-bold;
			letter-spacing: 1px;
			color: $color-white;
			white-space: nowrap;
		} 

		&:first-child,
		&:nth-child(5) {
			margin-left: 0;
		}

		&:nth-child(4),
		&:last-child {
			margin-right: 0;
		}


		&.is-active {
			.nav-dropdown-box {
				.sub-nav {
					visibility: visible;
					opacity: 1;
				}
		
		@media (min-width: 1280px) {
				&:hover {
					.sub-nav {
						visibility: visible;
						opacity: 1;
					}
				}					
		}

			}

			.nav-dropdown-box > a::before {
				border-top: 0;
				border-bottom: 6px solid $color-white;
			}

		}		

	}
}
.nav-dropdown-box {
	display: inline-block;
	position: relative;

		> a {
			text-transform: uppercase;
			font: 14px/16px $font-family-halvar-bold;
			letter-spacing: 1px;
			color: $color-white;
			position: relative;
			white-space: nowrap;

			&::before {
				@extend .before;
				border: 4px solid transparent;
			 border-top: 6px solid $color-white; 
			 top: 50%;
			 right: -15px;
			 margin-top: -3px;
			 transition: border 0.3s;
			}

		} 	
	
	@media (min-width: 1280px) {
		&:hover {
			.sub-nav {
				visibility: visible;
				opacity: 1;
			}
		}
	}

}
.sub-nav {
	display: none;
	background: $color-white;
	text-align: left;
	padding: 23px 20px 22px;

	@media (min-width: 768px) {
		display: block;
		position: absolute;
		box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.20);	
		border-radius: 3px;
		left: 50%;
		top: 32px;
		transform: translateX(-50%);
		transition: opacity 0.3s visibility 0.3s;
		opacity: 0;
		visibility: hidden;
		z-index: 20;
	}

	&::before {
		@extend .before;
		width: 14px;
		height: 14px;
		left: 50%;
		top: 0;
		margin-left: -7px;
		margin-top: -7px;
		background: $color-white;
		transform: rotate(45deg); 
	}

	&::after {
		@extend .before;
		width: 100%;
		height: 17px;
		left: 0;
		top: -17px;
	}	

	li {
		@extend .d-block;;
		margin-bottom: 21px;

		&:last-child {
			margin-bottom: 0;
		}

		a {
			display: block;
			color: $color-blue2;
			font-weight: 300;
			white-space: nowrap;
		 font: 14px/14px $font-family-inter-regular;

			&:hover {
				color: $color-blue;
			}

		}
	}
}

