}
@-webkit-keyframes sbs-anim-rec {
	0% {
		opacity:1;
		transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);
		animation-timing-function:linear;
	}
	100% {
		opacity:1;
		transform:translateX(-1523px)translateY(0px)rotate(0deg)scale(1,1);
	}
}
@keyframes sbs-anim-rec {
	0% {
		opacity:1;
		transform:translateX(0px)translateY(0px)rotate(0deg)scale(1,1);
		animation-timing-function:linear;
	}
	100% {
		opacity:1;
		transform:translateX(-1523px)translateY(0px)rotate(0deg)scale(1,1);
	}
}

