.nav-hide {
	display: none;
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 10;
	cursor: pointer;
	left: 0;
	top: 0;
}
.touch-nav {
	display: none;
	width: 35px;
	height: 35px;
	border-radius: 17.5px;
	background: $color-blue;
	margin-left: 20px;
	padding: 9px 7.5px 0;

	i {
		width: 20px;
		height: 3px;
		margin-bottom: 4px;
		border-radius: 1.5px;
		background: $color-white;
		display: block;
		transition: all .3s;

		&:last-child {
			margin-bottom: 0;
		}
	}
	&.is-active {
		i {
			width: 25px;

			&:first-child {
				transform: rotate(45deg);
				transform-origin: 10% 50%;
			}
			&:nth-child(2) {
				opacity: 0;
			}
			&:last-child {
				transform-origin: 10% 50%;
				transform: rotate(-45deg);
			}
		}
	}
}

.nav-close {
	right: 10px;
	top: 8px;
	position: absolute;
	z-index: 20;
	display: none;
	width: 35px;
	height: 35px;
	border-radius: 17.5px;
	padding: 9px 7.5px 0;

	i {
		width: 25px;
		height: 3px;
		margin-bottom: 4px;
		border-radius: 1.5px;
		background: $color-white;
		display: block;
		transition: all .3s;

		&:first-child {
			transform: rotate(45deg);
			transform-origin: 10% 50%;
		}
		&:nth-child(2) {
			opacity: 0;
		}
		&:last-child {
			transform-origin: 10% 50%;
			transform: rotate(-45deg);
			margin-bottom: 0;
		}		
	}

}
.btn-tl-show {
	cursor: pointer;
	right: 0;
	top: 50%;
	margin-top: -28px;
	position: absolute;
	z-index: 5;
	display: none;
	width: 56px;
	height: 56px;
	transition: transform 0.3s;	

	&::before, &::after {
		@extend .before;
		background: $color-blue;
		left: 50%;
		top: 50%;
	}
	&::before {
		width: 24px;
		height: 2px;
		margin-top: -1px;
		margin-left: -12px;
	}
	&::after {
		width: 2px;
		height: 24px;
		margin-top: -12px;
		margin-left: -1px;		
	}

	&.is-active {
		transform: rotate(-45deg);   	
	}

}

